/*–ü–æ–¥—Å—á–∏—Ç–∞—Ç—å —Å—É–º–º—É –≤—Å–µ—Ö —á–∏—Å–µ–ª –≤ –∑–∞–¥–∞–Ω–Ω–æ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –¥–∏–∞–ø–∞–∑–æ–Ω–µ.*/

const hwThreeTaskOne = document.getElementById("hw3.1");
hwThreeTaskOne.addEventListener("click", function() {
    const firstNumberRange = Number(prompt('–í–≤–µ–¥–∏—Ç–µ –¥–∏–∞–ø–æ–∑–æ–Ω —á–∏—Å–µ–ª. –í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ –û–¢:', '1'));
    const lastNumberRange = Number(prompt(' –í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ –î–û:', '10'));
    let sumOfNumbers = 0;

    for( let i = firstNumberRange; i <= lastNumberRange ; i++){
        sumOfNumbers +=i;
    }

    alert(`–°—É–º–º–∞ —á–∏—Å–µ–ª –≤ –¥–∏–∞–ø–æ–∑–æ–Ω–µ [${firstNumberRange} ; ${lastNumberRange}] –±—É–¥–µ—Ç ${sumOfNumbers}`);
});

//2
/*–ó–∞–ø—Ä–æ—Å–∏—Ç—å 2 —á–∏—Å–ª–∞ –∏ –Ω–∞–π—Ç–∏ —Ç–æ–ª—å–∫–æ –Ω–∞–∏–±–æ–ª—å—à–∏–π –æ–±—â–∏–π –¥–µ–ª–∏—Ç–µ–ª—å.*/

const hwThreeTaskTwo = document.getElementById("hw3.2");
hwThreeTaskTwo.addEventListener("click", function() {
    const firstNumberUser = Number(prompt('–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤–æ–µ —á–∏—Å–ª–æ:', '24'));
    const secondNumberUser = Number(prompt(' –í–≤–µ–¥–∏—Ç–µ –≤—Ç–æ—Ä–æ–µ —á–∏—Å–ª–æ:', '12'));
    let firstListOfNumbers = [];
    let secondListOfNumbers = [];
    
    for (let i = 1; i <= firstNumberUser; i++) {
        if(firstNumberUser % i === 0) {
            firstListOfNumbers.push(i);
        }
    }
    for (let i = 1; i <= secondNumberUser; i++) {
        if(secondNumberUser % i === 0) {
            secondListOfNumbers.push(i);
        }
    }
    
    let maxDivider = 0;
    for (let i = 0; i < firstListOfNumbers.length; i++) {
        for (let j = 0; j < secondListOfNumbers.length; j++) {
             if(firstListOfNumbers[i] === secondListOfNumbers[j] ){

                if(maxDivider > firstListOfNumbers[i]){
                    continue;
                }

                else if(maxDivider < firstListOfNumbers[i]){
                    maxDivider = firstListOfNumbers[i];
                }
             }
        } 
    }
   
    alert(`–ù–∞–∏–±–æ–ª—å—à–∏–π –æ–±—â–∏–π –¥–µ–ª–∏—Ç–µ–ª—å —á–∏—Å–µ–ª ${firstNumberUser} –∏ ${secondNumberUser} –±—É–¥–µ—Ç ${maxDivider}`);
});

