//5
/*–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 10 —á–∏—Å–µ–ª –∏ –ø–æ–¥—Å—á–∏—Ç–∞—Ç—å, —Å–∫–æ–ª—å–∫–æ –æ–Ω –≤–≤–µ–ª –ø–æ–ª–æ–∂–∏—Ç–µ–ª—å–Ω—ã—Ö, –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã—Ö –∏ –Ω—É–ª–µ–π. 
–ü—Ä–∏ —ç—Ç–æ–º —Ç–∞–∫–∂–µ –ø–æ—Å—á–∏—Ç–∞—Ç—å, —Å–∫–æ–ª—å–∫–æ —á–µ—Ç–Ω—ã—Ö –∏ –Ω–µ—á–µ—Ç–Ω—ã—Ö. –í—ã–≤–µ—Å—Ç–∏ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É –Ω–∞ —ç–∫—Ä–∞–Ω. 
–£—á—Ç–∏—Ç–µ, —á—Ç–æ –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –æ–¥–Ω–æ–π –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π (–Ω–µ 10) –¥–ª—è –≤–≤–æ–¥–∞ —á–∏—Å–µ–ª –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º.*/

const hwThreeTaskFive = document.getElementById("hw3.5");
hwThreeTaskFive.addEventListener("click", function() {
    const amountOfNumbers = Number(prompt('–°–∫–æ–ª—å–∫–æ –±—É–¥–µ—Ç–µ –≤–≤–æ–¥–∏—Ç—å —á–∏—Å–µ–ª?:', '3'));
    let positiveListNumbers = 0 ;
    let negativeListNumbers = 0 ;
    let listOfZero = 0;
    let evenNumbers = 0;
    let oddNumbers = 0;
    let arrOfNumbers = [];
    for (let i = 0; i < amountOfNumbers; i++)
    {
        let userNumber = Number(prompt(`–í–≤–µ–¥–∏—Ç–µ ${i+1} —á–∏—Å–ª–æ(-5,6,0...):`, ''));
        arrOfNumbers.push(userNumber);

        if(userNumber !== 0){

            if(userNumber % 2 === 0) {
                evenNumbers += 1;

                if(userNumber > 0){
                    positiveListNumbers +=1;
                }
                else{
                     negativeListNumbers +=1;
                }
            }
            else{
                oddNumbers += 1;

                if(userNumber > 0){
                    positiveListNumbers +=1;
                }
                else{
                     negativeListNumbers +=1;
                }
            }
        }

        else {
            listOfZero +=1;
        }

    }

    alert(` 
    –í–≤–µ–¥—ë–Ω–Ω—ã–µ –ß–∏—Å–ª–∞ : ${arrOfNumbers}
    –ø–æ–∑–∏—Ç–∏–≤–Ω—ã–µ : ${positiveListNumbers};
    –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã–µ : ${negativeListNumbers};
    –Ω—É–ª–∏ : ${listOfZero};
    —á—ë—Ç–Ω—ã—Ö : ${evenNumbers};
    –Ω–µ—á—ë—Ç–Ω—ã—Ö : ${oddNumbers};
    `);
   
});

//6
/*–ó–∞—Ü–∏–∫–ª–∏—Ç—å –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä. –ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 2 —á–∏—Å–ª–∞ –∏ –∑–Ω–∞–∫, —Ä–µ—à–∏—Ç—å –ø—Ä–∏–º–µ—Ä, 
–≤—ã–≤–µ—Å—Ç–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç –∏ —Å–ø—Ä–æ—Å–∏—Ç—å, —Ö–æ—á–µ—Ç –ª–∏ –æ–Ω —Ä–µ—à–∏—Ç—å –µ—â–µ –æ–¥–∏–Ω –ø—Ä–∏–º–µ—Ä.
 –ò —Ç–∞–∫ –¥–æ —Ç–µ—Ö –ø–æ—Ä, –ø–æ–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –æ—Ç–∫–∞–∂–µ—Ç—Å—è.*/

 const hwThreeTaskSix = document.getElementById("hw3.6");
 hwThreeTaskSix.addEventListener("click", function() {

    let continueCalc = Number(prompt('–ó–∞–ø—É—Å—Ç–∏—Ç—å –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä?', ''));

    while(continueCalc){

    const firstNumberUser = Number(prompt('–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤–æ–µ —á–∏—Å–ª–æ:', '2'));
    const operator = prompt('–í–≤–µ–¥–∏—Ç–µ [+ - / *]:', '');
    const secondNumberUser = Number(prompt(' –í–≤–µ–¥–∏—Ç–µ –≤—Ç–æ—Ä–æ–µ —á–∏—Å–ª–æ:', '2'));
    let result = 0;
    switch(operator) {
        case '+': {
          result = firstNumberUser + secondNumberUser;
          break;
        }
        case '-': {
            result = firstNumberUser - secondNumberUser;
            break;
        }
        case '/': {
            result = firstNumberUser / secondNumberUser;
            break;
          }
          case '*': {
            result = firstNumberUser * secondNumberUser;
            break;
          }
          default: {
            alert(`–û—à–∏–±–∫–∞ –≤–≤–æ–¥–∞!`);
            break;
          }
    }
    alert(`${result}`);

    continueCalc = Number(prompt('–ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å? ', ''));
};

});

//7
/*–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —á–∏—Å–ª–æ –∏ –Ω–∞ —Å–∫–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä –µ–≥–æ —Å–¥–≤–∏–Ω—É—Ç—å. 
–°–¥–≤–∏–Ω—É—Ç—å —Ü–∏—Ñ—Ä—ã —á–∏—Å–ª–∞ –∏ –≤—ã–≤–µ—Å—Ç–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç (–µ—Å–ª–∏ —á–∏—Å–ª–æ 123456 —Å–¥–≤–∏–Ω—É—Ç—å –Ω–∞ 2 —Ü–∏—Ñ—Ä—ã, —Ç–æ –ø–æ–ª—É—á–∏—Ç—Å—è 345612).*/

const hwThreeTaskSeven = document.getElementById("hw3.7");
hwThreeTaskSeven.addEventListener("click", function() {

   const userNumber = Number(prompt('–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ:', '123456'));
   const digitShift = Number(prompt('–ù–∞ —Å–∫–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä —Å–¥–≤–∏–Ω—É—Ç—å —á–∏—Å–ª–æ?:', '2'));
   let userNumberString = userNumber.toString().split("");
    let partUserNumber ;
    let i = 0;
    while(i < digitShift){
        partUserNumber = userNumberString.shift();
        userNumberString.push(partUserNumber);
        i++;
    }
    alert(userNumberString.join(""));
    
});

