/* –ó–∞—Ü–∏–∫–ª–∏—Ç—å –≤—ã–≤–æ–¥ –¥–Ω–µ–π –Ω–µ–¥–µ–ª–∏ —Ç–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º: ¬´–î–µ–Ω—å –Ω–µ–¥–µ–ª–∏.
 –•–æ—Ç–∏—Ç–µ —É–≤–∏–¥–µ—Ç—å —Å–ª–µ–¥—É—é—â–∏–π –¥–µ–Ω—å?¬ª –∏ —Ç–∞–∫ –¥–æ —Ç–µ—Ö –ø–æ—Ä, –ø–æ–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–∞–∂–∏–º–∞–µ—Ç OK.*/ 

